import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { PaymentDetailsService } from 'src/app/service/payment-details.service';
declare var swal: any;

@Component({
  selector: 'app-payment-details-add',
  templateUrl: './payment-details-add.component.html'
})
export class PaymentDetailsAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;
  selectedFile: File;
  editImageUrl: any = null;
  paymentTypeList: any = [];
  showLoader = false;
  formErrors = {
    buyer_id: null,
    apierror: null
  };
  isEditing = false;
  editId = null;


  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private paymentDetailsservice: PaymentDetailsService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      payment_type: [1],
      satus:[1],
      buyer_id: [null],
      seller_id: [null],
      total_amount: [null, Validators.compose([Validators.required])],
      cart_id: [null],
      payment_gateway_response: [null],
    });
  }

  ngOnInit() {

  }

  editObject() {
    this.loadingState = true;
    this.paymentDetailsservice.getPaymentDetailsById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.editImageUrl = response.data.image,
          this.addForm.patchValue({
            payment_type: response.data.payment_type,
            satus: response.data.satus,
            buyer_id: response.data.buyer_id,
            seller_id: response.data.seller_id,
            total_amount: response.data.total_amount,
            cart_id: response.data.cart_id,
            payment_gateway_response:response.data.payment_gateway_response
          });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('/payment-details');
    });
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].buyer_id.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
    }
  }

  removeFile() {
    this.selectedFile = null;
  }

  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      if (this.isEditing) {
        this.paymentDetailsservice.editPaymentDetails(this.editId, formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/payment-details');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('buyer_id')) {
                this.formErrors.buyer_id = obj.buyer_id;
              }

            });
          }
        });
      } else {
        this.paymentDetailsservice.addPaymentDetails(formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/payment-details');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('buyer_id')) {
                this.formErrors.buyer_id = obj.buyer_id;
              }
            });
          }
        });
      }
    }
  }
}
